






































































import poster from './poster.vue'
import relation from '../../components/relation.vue'

export default {
  components: {
    poster,
    relation
  },
  data() {
    return {
      banner: [],
      tabLists: [
        {
          name: '待唤醒'
        },
        {
          name: '已唤醒'
        }
      ],
      tabIndex: 0,
      loading: true,
      teamLists: [],
      currentPage: 1, // 当前页
      maxPage: 1, // 最大页
      wechatFlag: false,
      wechatStr: '',
      wechatQrCode: '',
      alertData: {},
      posterData: {},
      relationData: {}
    }
  },
  computed: {
    config() {
      return this.$store.state.config
    },
    defaultAvatar() {
      return '../../static/images/avatar.png'
    },
    isLoad() {
      return parseInt(this.maxPage) <= parseInt(this.currentPage) && this.teamLists.length > 0
    },
    showNoRecord() {
      return this.teamLists.length <= 0 && !this.loading
    }
  },
  onLoad() {
    this.$http({
      url: this.$apis.articles,
      method: 'GET',
      data: {
        cat_id: 12
      },
      cb: (err, data) => {
        if (!err && data.success) {
          this.banner = data.data
          this.banner.forEach(item => {
            item.content = item.content.replace(/\<img/gi, '<img style="display:block;max-width:100%;height:auto" ')
          })
        }
      }
    })
    this.loading = false
    this.updateLists()
  },
  methods: {
    tabClick(n) {
      if (this.tabIndex === n || this.loading) return
      this.tabIndex = n
      this.currentPage = 1
      this.updateLists()
    },
    updateLists() {
      if (this.loading) return
      this.loading = true
      if (this.currentPage <= 1) {
        this.teamLists = []
      }
      switch (this.tabIndex) {
        case 0:
          this.$http({
            url: this.$apis.waitforCall,
            method: 'GET',
            data: {
              page: this.currentPage
            },
            cb: (err, data) => {
              if (!err && data.success) {
                this.currentPage = data.data.current_page
                this.maxPage = data.data.last_page
                let oldArr = this.teamLists
                let newArr = data.data.data
                this.teamLists = oldArr.concat(newArr)
              } else {
                uni.showToast({
                	title: '服务器繁忙，请稍后再试',
                  icon: 'none'
                })
              }
              this.loading = false
            }
          })
          break
        case 1:
          this.$http({
            url: this.$apis.alreadrCall,
            method: 'GET',
            data: {
              page: this.currentPage
            },
            cb: (err, data) => {
              if (!err && data.success) {
                this.currentPage = data.data.current_page
                this.maxPage = data.data.last_page
                let oldArr = this.teamLists
                let newArr = data.data.data
                this.teamLists = oldArr.concat(newArr)
              } else {
                uni.showToast({
                	title: '服务器繁忙，请稍后再试',
                  icon: 'none'
                })
              }
              this.loading = false
            }
          })
          break
      }
    },
    scrollUpdate() {
      if (!this.loading && !this.isLoad && this.teamLists.length > 0) {
        this.currentPage ++
        this.updateLists()
      }
    },
    showWechat(str, url) {
      if (!str && !url) return
      this.wechatStr = str
      this.wechatQrCode = url || ''
      this.wechatFlag = true
    },
    hideWechat() {
      this.wechatFlag = false
    },
    call(num) {
      if (!num) {
        uni.showToast({
        	title: '用户未填写手机号码',
          icon: 'none'
        })
      } else {
        uni.makePhoneCall({
        	phoneNumber: num
        })
      }
    },
    copy(str) {
      uni.setClipboardData({
      	data: str,
        success: () => {
          uni.showModal({
            title: '复制成功',
            content: '微信已复制，请打开微信查找添加',
            showCancel: false
          })
        },
        fail: () => {
          uni.showModal({
            title: '复制失败',
            content: str,
            showCancel: false
          })
        },
        complete: () => {
        	this.hideWechat()
        }
      })
    },
    showVipExplain(b) {
      this.relationData = {
        show: b
      }
    },
    createPoster(item) {
      let type = 8
      if (item.role >= 2 || (item.role === 1 && item.is_finish === 1)) {
        // 如果是VIP，或者完成任务的普通会员
        type = 5
        this.posterData = {
          show: true,
          type: type
        }
      } else if (item.role === 0 && item.bonus !== '0.00') {
        // 余额不为0的临时用户
        type = 6
        this.posterData = {
          show: true,
          type: type,
          uid: item.id
        }
      } else if (item.role === 0 && item.bonus === '0.00') {
        // 余额为0的临时用户
        type = 7
        this.posterData = {
          show: true,
          type: type
        }
      } else if (item.role === 1 && item.is_finish === 0) {
        // 未完成任务的普通会员
        type = 9
        this.posterData = {
          show: true,
          type: type
        }
      }
    },
    posterError() {
      uni.showToast({
      	title: '海报生成失败',
        icon: 'none'
      })
    }
  }
}
